img {
  max-width: 100%;
  height: auto; }

*,
::after,
::before {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

.container {
  margin: 0 auto;
  padding: 0; }
  @media screen and (max-width: 1023px) {
    .container {
      width: 100%;
      padding: 0 20px; } }
  @media screen and (max-width: 767px) {
    .container {
      padding: 0 16px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "Open Sans", "Arial", sans-serif; }

.page__body {
  margin: 0; }

.header {
  background: url("/img/bike-background-desktop.jpg") no-repeat;
  background-position: center;
  min-height: 825px; }

.main-nav {
  width: 873px;
  padding-top: 21px; }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding-bottom: 9px; }

.main-nav__link {
  text-decoration: none;
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: -0.005em;
  color: rgba(255, 255, 255, 0.7);
  opacity: 0.7; }
